<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{name}}</h4></li>
      <li class="collection-item">ID#: {{employee_id}}</li>
      <li class="collection-item">Tag: {{dept}}</li>
      <li class="collection-item">Todo Item: {{position}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteEmployee" class="btn green">Check Off</button>
    <div class="fixed-action-btn">
      <router-link v-bind:to="{ name: 'edit-employee', params: { employee_id: employee_id }}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit'

  export default {
    name: 'view-employee',
    data () {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null,
        points:[],
        newPoints: null
      }
    },
    created () {
      db.collection('points').get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            'id': doc.id,
            'currentPoints': doc.data().currentPoints,
          }
          this.points.push(data)
        })
      })
    },
    beforeRouteEnter (to, from, next) {
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next(vm => {
            vm.employee_id = doc.data().employee_id
            vm.name = doc.data().name
            vm.dept = doc.data().dept
            vm.position = doc.data().position
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.employee_id = doc.data().employee_id
            this.name = doc.data().name
            this.dept = doc.data().dept
            this.position = doc.data().position
          })
        })
      },
      deleteEmployee () {
        
        
        var pointRef = db.collection('points').doc('YyzAmfuFFlKZlKsfEFZX');

        var transaction = db.runTransaction(t => {
        return t.get(pointRef)
        .then(doc => {
        // Add one person to the city currentPoints
        var newPoint = doc.data().currentPoints + 5;
        t.update(pointRef, {currentPoints: newPoint});
         });
          }).then(result => {
            console.log('Transaction success!');
        }).catch(err => {
          console.log('Transaction failure:', err);
      });

        if(confirm ('You Gained 5 Points! Keep It Up!')) {
          db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              this.$router.push('/')
            })
          })
        }
      }
    }
  }
</script>



