<template>
  <div id="view-store">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>
          <li v-for="point in points" v-bind:key="point.id" class="collection-item">Store points: {{point.currentPoints}}</li>
        </h4>
      </li>
      <li>
         <h6 class="collection-item">1 Hour of TV Cost:10 points<button @click="buyTVTime" class="secondary-content fa fa-shopping-cart"></button></h6>
         <h6 class="collection-item">Snack Cost:30 points<button @click="buySnack" class="secondary-content fa fa-shopping-cart"></button></h6>
         <h6 class="collection-item">Buy New Game Cost:50 points<button @click="buyGame" class="secondary-content fa fa-shopping-cart"></button></h6>
         <h6 class="collection-item">Buy New Game Cost:50 points<button @click="test" class="secondary-content fa fa-shopping-cart"></button></h6>
      </li>
    </ul>
     <router-link to="/" class="btn grey">Back</router-link>
  </div>
</template>

<script>
  import db from './firebaseInit'

  export default {
    name: 'view-store',
    data () {
      return {
        points: []
      }
    },
    created () {
      db.collection('points').get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            'id': doc.id,
            'currentPoints': doc.data().currentPoints
          }
          this.points.push(data)
        })
      })
    },
    methods: {
      buyTVTime(){
       var pointRef = db.collection('points').doc('YyzAmfuFFlKZlKsfEFZX');

        var transaction = db.runTransaction(t => {
          return t.get(pointRef)
          .then(doc => {
            var testPoint = doc.data().currentPoints;
            var newPoint = doc.data().currentPoints;
            if(testPoint >=10){
              if(confirm ('Buy TV Time for 10 Points!')){
                var newPoint = doc.data().currentPoints - 10;
                this.$router.push({name:'Home'})
              }
            }
            else{
              if(confirm ('Sorry Need More Points!')){
                var newPoint = doc.data().currentPoints;
              }
            }
            t.update(pointRef, {currentPoints: newPoint});
            });
            })
            .then(result => {
              console.log('Transaction success!');
            }).catch(err => {
              console.log('Transaction failure:', err);
          });
      },
      buySnack(){
        var pointRef = db.collection('points').doc('YyzAmfuFFlKZlKsfEFZX');

        var transaction = db.runTransaction(t => {
          return t.get(pointRef)
          .then(doc => {
            var testPoint = doc.data().currentPoints;
            var newPoint = doc.data().currentPoints;
            if(testPoint >=30){
              if(confirm ('Buy Snack for 30 Points!')){
                var newPoint = doc.data().currentPoints - 30;
              
              }
            }
            else{
              if(confirm ('Sorry Need More Points!')){
                var newPoint = doc.data().currentPoints;
              }
            }
            t.update(pointRef, {currentPoints: newPoint});
            });
            })
            .then(result => {
              console.log('Transaction success!');
            }).catch(err => {
              console.log('Transaction failure:', err);
          });
      },
      buyGame(){
       var pointRef = db.collection('points').doc('YyzAmfuFFlKZlKsfEFZX');

        var transaction = db.runTransaction(t => {
          return t.get(pointRef)
          .then(doc => {
            var testPoint = doc.data().currentPoints;
            var newPoint = doc.data().currentPoints;
            if(testPoint >=50){
              if(confirm ('Buy New Game for 50 Points!')){
                var newPoint = doc.data().currentPoints - 50;
              
              }
            }
            else{
              if(confirm ('Sorry Need More Points!')){
                var newPoint = doc.data().currentPoints;
              }
            }
            t.update(pointRef, {currentPoints: newPoint});
            });
            })
            .then(result => {
              console.log('Transaction success!');
            }).catch(err => {
              console.log('Transaction failure:', err);
          });
      },
      test(){
        
      }
    }
  }
</script>



